let quote = [];
// export default App;
export default class App extends React.Component {
    state = {
      data: []
    }
  
    componentDidMount() {
      axios.get(`https://quitable-api.herokuapp.com/quotes/`)
        .then(res => {
          const data = res.data;
          this.setState({ data });
          quote.push(data[0].content);
        }).catch((error)=>{
            console.log(error);
        })
    }
  
    render() {
        return(<div>
            <Toast/>
            
            <div className="content-wrapper">
            <Navbar brandname="Quotable"/>
            <Header title="A RESTFul Api Which Generates Quotes From Different Famous authors in Different Varities."/>
            <div className="container-fluid" style={{"marginTop":"150px"}}>
            <QuoteOfDay content={this.state.data.length===0?"Please Wait......":this.state.data[this.state.data.length-1].content} name={this.state.data.length===0?"Developer":this.state.data[this.state.data.length-1].author}/>
            <br/>
        <h1 style={{"fontSize":"25px","marginLeft":"25px"}}> {this.state.data.length===0?"Please Wait....":"Total Quotes " +String(this.state.data.length)}</h1>
              <div className="row">
                
                {this.state.data.map(createCard)}
                </div>
            
             </div>
            </div>
            </div>)
    }
  }