import React from "react"
import halfmoon from "halfmoon"




function Card(props){

    // Toasts success alert
  function toastSuccessAlert(event) {
    
    console.log(event.target.parentElement.parentElement.querySelector('p').textContent);
    let data=event.target.parentElement.parentElement.querySelector('p').textContent;
    navigator.clipboard.writeText(data).then(()=>{
        halfmoon.toastAlert('precompiled-alert-1', 1500);
    }).catch((error)=>{
        console.log(error);
    })
  }
  //redirect the user to the main api to get quotes by its tag

  function redirect(event){
    console.log(event.target.textContent);
    window.location = `https://quitable-api.herokuapp.com/quotes/bytag/${event.target.textContent}`
  }


    return(
        <div>


        <div className="card">
         
         
          <div className="content">
            <h2 className="content-title">
              {props.name}
            </h2>
            <p className="text-muted text-white" style={{'fontSize':"20px"}}>
              &quot;{props.content}&quot;
            </p>
            <button className="btn btn-primary" style={{"marginRight":"15px"}} onClick={redirect}>{props.tag}</button><br/><br/>
              <button className="btn btn-danger" onClick={toastSuccessAlert}>Copy</button>
            
          </div>
        </div>
      </div>
);
}

export default Card;